import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import Avatar from "@material-ui/core/Avatar";
import { makeStyles } from "@material-ui/core/styles";

import classes from "./Header.module.css";

const useStyles = makeStyles((theme) => ({
  large: {
    width: theme.spacing(20),
    height: theme.spacing(20),
  },
}));

const Header = (props) => {
  const { info } = props;
  const cls = useStyles();

  return (
    <div className={classes.header}>
      <Container>
        <Grid
          justify="center"
          alignItems="center"
          alignContent="center"
          container
          spacing={2}
        >
          <Grid item xs={12} md={2}>
            <div className={classes.avatar}>
              <div>
                <Avatar
                  src={info.image}
                  alt={info.name}
                  className={cls.large}
                />
              </div>
              <h2>{info.name}</h2>
            </div>
          </Grid>
          <Grid item xs={12} md={10}>
            <div className={classes.info}>
              <span className={classes.title}>{info.role}</span>
              <div>
                <div className={classes.url}>
                  <i className={`${info.phone.icon} icon`}></i>
                  <a href={`tel:${info.phone.text}`}>{info.phone.text}</a>
                </div>
                <div className={classes.url}>
                  <i className={`${info.mail.icon} icon`}></i>
                  <a href={`mailto:${info.mail.text}`}>{info.mail.text}</a>
                </div>
              </div>
            </div>
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

export default Header;
