import classes from "./LanguageItem.module.css";

const LanguageItem = (props) => {
  const { lang } = props;

  const fluent = new Array(lang.fluent)
    .fill(true)
    .map((level, idx) => (
      <div
        key={idx}
        className={`${classes.block} ${classes[`block-${idx}`]}`}
      ></div>
    ));

  return (
    <div className={classes.outer}>
      {fluent}
      <div className={classes.inner}>
        <p className={classes.text}>{lang.text}</p>
        <p className={classes.level}>{lang.level}</p>
      </div>
    </div>
  );
};

export default LanguageItem;
