import Title from "../Title/Title";
import ExperienceItem from "./ExperienceItem";

const Experience = (props) => {
  const { data } = props;

  return (
    <div>
      <Title title="experience" />
      <div>
        {data.map((ex) => (
          <ExperienceItem key={ex.company} ex={ex} />
        ))}
      </div>
    </div>
  );
};

export default Experience;
